import React from 'react';
import './Nav.css';
import {Fade} from 'react-awesome-reveal';

const navSections = ["Inicio","Habilidades","Proyectos","Contacto"];

export default function Nav(props) {
  return (
    <div className='nav-container'>
        <nav className='header-nav'>
            <ul className='nav-list'>
                <Fade cascade>
                {navSections.map((section, index) => (
                  <li><a key={index} onClick={() => props.func(index)}
                   className={index == props.actualIndex? 'nav-item selectedItem' : 'nav-item'}>
                    {section}
                    </a></li>
                ))}
                </Fade>
            </ul>
        </nav>
    </div>
  )
}
